.current {color: #ebd2a2 !important;
}

/* root colors borrowed from Goat. Update. Also hate my colors, find better ones */

:root {
    --main-bg-color: #FFE7EC;
    --primary-accent-color: #fcb77a;
    --secondary-accent-color: #faffc0;
    --tertiary-accent-color: #c0ffe4;
    --tertiary-accent-color-dark: #a8dfc8 ;
    --link-solid-color: black;
    --link-standout-color: #faffc0;
    --link-standout-highlight: #c0ffe4;
    color: black;
}

body {

    background-color: #c7877f;
    background-image: linear-gradient(180deg, #c7877f 25%, #d3a38a 85%, #ebd2a2 100%);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;

    
    max-width: 65%;
    margin: auto;
    text-align: center;
    

    border-left-style: solid;
    border-right-style: solid;
    border-width: thick;
    border-color: #946658;
}

html {
    height: 100dvh;}

#page-container {
    position: relative;
    min-height: 100vh;
    background-color: #ffebb0;
    
    
    margin: 0;
}

/*Grid */

.item1 { grid-area: header;}
.nav { grid-area: nav;}
.left-menu { grid-area: menu;}
.item3 { grid-area: main;}
.item4 {grid-area: right;}
.footer {grid-area: footer;}

.grid-container {
    display: grid;
    grid-template-areas:
    'header header header header header header'
    '. nav nav nav nav .'
    'menu main main main main right'
    'footer footer footer footer footer footer';
    grid-gap: 10px;
    padding: 10px;
}
.grid-container > div {
    background-color: #faffc0;
    text-align: center;
    padding: 20px 0;
    font-size: 30px;
    border-style: dashed;
    border-color: #c7877f;
    
}
/* navigaton */
ul.nav {
    background-color: #946658; 
    margin: 0;
    padding: 0;
    border-style: outset;
    border-width: thick;
    border-color: #a8dfc8;
    }

.nav{
    li {
        float: center;
        font-size: 1em;
        
        
    }

    li a, .dropbtn {
        display: inline-block;
        color: var(--link-solid-color);
        text-align: center;
        padding: 14px 16px;
        text-decoration: none;
        font-size: 1em;
    }

    li a:hover, .dropdown:hover .dropbtn {
        background-color: var(--secondary-accent-color);
    }

    li.dropdown {
    display: inline-block;
    }

    .dropdown-content {
        display: none;
        position: absolute;
        /*background-color: #f9f9f9;*/
        background-color: var(--tertiary-accent-color);
        min-width: 160px;
        box-shadow: 0px 8px 16px 8px rgba(0,0,0,0.2);
        z-index: 1;
    }

    .dropdown-content a {
        color: var(--link-solid-color);
        padding: 12px 16px;
        text-decoration: none;
        display: block;
        text-align: left;
    }

    .dropdown-content a:hover {
        background-color: var(--tertiary-accent-color-dark);
    }

    .dropdown:hover .dropdown-content {
        display:block;
    }
}

/* left menu */
.left-menu-container{
    text-align: left;
}

/* Book Case */
.bookcase {
    grid-area: main;
    grid-template-rows: auto;
}

.shelf {
    width: 100%;
}

.book-container {
    background-color: #faffc0;
    text-align: center;
    padding: 20px 0;
    font-size: 30px;
    border-style: solid;
    border-color: #c7877f;

}

.card {
    position: relative;
    display: inline-block;
    width: 20%;
    border: 5px red solid;
  }
  .card .img-top {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99;
  }
  .card:hover .img-top {
    display: inline;
  }

